/**
 * Image Collage Editor Styles
 */

.wp-block.wp-block-multi-block-mayhem-swatch-cards {

	.block-editor-inner-blocks {

		.block-editor-block-list__layout {
			display: grid;
			grid-template-columns: repeat(
				var(--mbm-swatch-cards-cols),
				minmax(0, 1fr)
			);
			grid-gap: var(--mbm-swatch-cards-gap);
		}
	}
}
